  position: relative;
  margin: 40px 0;
  padding: 20px 0;
}

.timeline-scroll-container {
  width: 100%;
  overflow-x: auto;
  margin-bottom: 20px;
}

.timeline-track {
  display: flex;
  position: relative;
  min-width: 100%;
  padding: 40px 20px 20px;
}

.timeline-track::before {
  content: '';
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.12);
  z-index: 1;
}

.timeline-event {
  position: relative;
  flex: 1;
  min-width: 120px;
  text-align: center;
  cursor: pointer;
  padding: 0 10px;
  transition: all 0.3s;
}

.timeline-event.active .timeline-dot {
  transform: scale(1.5);
  box-shadow: 0 0 0 4px rgba(25, 118, 210, 0.2);
}

.timeline-date {
  margin-bottom: 15px;
  font-size: 12px;
  font-weight: 500;
}

.timeline-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #1976D2;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  transition: transform 0.3s;
}

.timeline-title {
  margin-top: 15px;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*  */
@media (max-width: 600px) {
  .timeline-event {
    min-width: 100px;
  }
  
  .timeline-title {
    font-size: 12px;
  }
}
</style>
